/* Analytics Dashboard Root */
.analytics-dashboard-root {
  width: 100%;
}
.analytics-dashboard-title {
  text-align: center;
  margin: 1.5rem 0 0.5rem;
  font-size: var(--text-2xl);
  font-weight: 700;
}
@media (max-width: 768px) {
  .analytics-dashboard-title {
    margin: 1rem 0 0.25rem;
    font-size: 1.2rem;
  }
}

/* Mobile Card */
.analytics-dashboard-mobile-card {
  margin: 24px auto;
  padding: 40px 24px 32px 24px;
  max-width: 370px;
  background: linear-gradient(135deg, #f4f6fa 80%, #dbeafe 100%);
  border: 1.5px solid #2563c7;
  border-radius: 18px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.09);
  text-align: center;
  color: #1e293b;
  font-size: 17px;
  font-weight: 500;
  letter-spacing: 0.01em;
  position: relative;
}
body[data-theme="dark"] .analytics-dashboard-mobile-card {
  background: linear-gradient(135deg, #23272f 80%, #2563c7 100%);
  color: #e0e6f0;
  box-shadow: 0 4px 16px rgba(0,0,0,0.22);
}
.analytics-dashboard-mobile-icon {
  font-size: 48px;
  margin-bottom: 18px;
  color: #2563c7;
  filter: drop-shadow(0 2px 6px #b6c6e6);
}
body[data-theme="dark"] .analytics-dashboard-mobile-icon {
  filter: drop-shadow(0 2px 6px #111);
}
.analytics-dashboard-mobile-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 8px;
}
.analytics-dashboard-mobile-desc {
  font-size: 15px;
  color: #334155;
  margin-bottom: 22px;
}
body[data-theme="dark"] .analytics-dashboard-mobile-desc {
  color: #b6c6e6;
}
.analytics-dashboard-mobile-btn {
  background: #2563c7;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 10px 22px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 2px 8px #b6c6e6;
  transition: background 0.2s;
}
body[data-theme="dark"] .analytics-dashboard-mobile-btn {
  box-shadow: 0 2px 8px #111;
}

/* Grid and Card Layout */
.analytics-dashboard-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 32px;
  margin: 32px 0;
}
@media (min-width: 900px) {
  .analytics-dashboard-grid {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 32px;
  }
}
.analytics-dashboard-card {
  background: var(--bg-card);
  border-radius: 12px;
  box-shadow: var(--shadow-card);
  padding: 16px;
  min-height: 340px;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--border-color);
}
@media (max-width: 768px) {
  .analytics-dashboard-card {
    min-height: 300px;
    padding: 8px;
  }
}
.analytics-dashboard-card-title {
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: var(--text-main);
}
.analytics-dashboard-divider {
  border: none;
  border-top: 1px solid var(--divider, #444);
  margin: 32px 0;
}
@media (max-width: 768px) {
  .analytics-dashboard-divider {
    margin: 16px 0;
  }
}

/* Chart backgrounds */
.analytics-dashboard-chart-bg {
  background: var(--bg-hover);
  border-radius: var(--radius-lg);
}

/* Holdings Grid and List */
.analytics-dashboard-holdings-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  margin: 32px 0;
  align-items: start;
}
@media (max-width: 900px) {
  .analytics-dashboard-holdings-grid {
    grid-template-columns: 1fr;
    gap: 24px;
    margin: 24px 0;
  }
}
.analytics-dashboard-holdings-list-wrapper {
  margin-top: 16px;
}
@media (max-width: 768px) {
  .analytics-dashboard-holdings-list-wrapper {
    margin-top: 10px;
  }
}
.analytics-dashboard-holdings-list-title {
  font-weight: 500;
  margin-bottom: 4px;
  font-size: 15px;
}
@media (max-width: 768px) {
  .analytics-dashboard-holdings-list-title {
    font-size: 13px;
  }
}
.analytics-dashboard-holdings-list-empty {
  color: #888;
  font-style: italic;
}
.analytics-dashboard-holdings-list-group {
  margin-top: 4px;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  background: #fafbfc;
  padding: 0;
  overflow: hidden;
}
body[data-theme="dark"] .analytics-dashboard-holdings-list-group {
  border: 1px solid #505050;
  background: #383838;
}
.analytics-dashboard-holdings-list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 16px;
  border-bottom: 1px solid #ececec;
  font-size: 14px;
  min-height: 40px;
  background: #fff;
}
body[data-theme="dark"] .analytics-dashboard-holdings-list-item {
  border-bottom: 1px solid #484848;
  background: #404040;
}
.analytics-dashboard-holdings-list-item-last {
  border-bottom: none;
}
.analytics-dashboard-holdings-list-name {
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 75%;
  font-size: 15px;
  color: inherit;
}
@media (max-width: 768px) {
  .analytics-dashboard-holdings-list-item {
    font-size: 12px;
    min-height: 36px;
    padding: 8px 12px;
  }
  .analytics-dashboard-holdings-list-name {
    font-size: 13px;
    max-width: 65%;
  }
}
.analytics-dashboard-holdings-list-held {
  color: #2563c7;
  font-weight: 700;
  font-variant-numeric: tabular-nums;
  margin-left: 12px;
  min-width: 60px;
  font-size: inherit;
  text-align: right;
}
body[data-theme="dark"] .analytics-dashboard-holdings-list-held {
  color: #4f8cff;
}
@media (max-width: 768px) {
  .analytics-dashboard-holdings-list-held {
    margin-left: 8px;
    min-width: 50px;
    font-size: 12px;
  }
} 