/* Footer styles extracted from styles.css */
.footer {
  margin-top: var(--space-4);
  padding-top: var(--space-4);
  border-top: 1px solid var(--border-color);
  color: var(--text-secondary);
  font-size: 0.9rem;
  padding-left: var(--space-3);
  padding-right: var(--space-3);
  padding-bottom: var(--space-4);
}
.footer-sections {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--space-4);
  margin-bottom: var(--space-4);
}
.footer-section h4 {
  font-size: 1rem;
  margin-bottom: var(--space-2);
  color: var(--text-main);
}
.footer-section p {
  margin-bottom: var(--space-2);
  line-height: 1.5;
  font-size: 0.85rem;
}
.footer-bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: var(--space-3);
  padding-top: var(--space-4);
  border-top: 1px solid var(--border-color);
  margin-top: var(--space-4);
}
.footer-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: var(--space-2);
}
.link-button {
  background: none;
  border: none;
  color: var(--primary);
  cursor: pointer;
  font-size: 0.9rem;
  padding: var(--space-1) var(--space-2);
  text-decoration: underline;
  min-height: 44px;
  display: inline-flex;
  align-items: center;
}
.link-button:hover {
  color: var(--primary-dark);
}
.copyright {
  color: var(--text-muted);
  font-size: var(--text-sm);
}
.footer-bottom-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-3);
}

@media (min-width: 390px) and (max-width: 768px) {
  .footer {
    margin-top: var(--space-5);
    padding-top: var(--space-5);
    padding-left: var(--space-4);
    padding-right: var(--space-4);
  }
  .footer-sections {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--space-4);
  }
  .footer-bottom {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
  }
  .footer-links {
    justify-content: flex-end;
  }
}
@media (max-width: 389px) {
  .footer {
    padding: 12px 0 0 0;
    font-size: 13px;
  }
  .footer-sections {
    grid-template-columns: 1fr;
    gap: var(--space-4);
  }
  .footer-bottom {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: var(--space-3);
    padding-top: var(--space-4);
    border-top: 1px solid var(--border-color);
    margin-top: var(--space-4);
  }
  .footer-links {
    justify-content: center;
  }
  .footer-section p {
    font-size: 0.85rem;
  }
}
@media (min-width: 769px) {
  .footer-sections {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--space-5);
  }
  .footer-section {
    min-width: 0;
  }
}
@media (min-width: 1200px) {
  .footer-bottom-row {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align: left;
  }
  .footer-links {
    justify-content: flex-start;
  }
  .copyright {
    text-align: right;
  }
} 

/* Privacy Policy Modal Styles */
.privacy-modal-overlay {
  display: flex;
  z-index: 11000;
  align-items: center;
  justify-content: center;
  background: rgba(0,0,0,0.18);
}
.privacy-modal-container {
  max-width: 650px;
  width: 95vw;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0,0,0,0.18);
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  background: var(--bg-card);
}
.privacy-modal-header {
  background: linear-gradient(135deg, #2563eb, #1e40af);
  padding: 28px 36px;
  color: white;
  display: flex;
  align-items: center;
  gap: 18px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
}
body[data-theme="dark"] .privacy-modal-header {
  background: linear-gradient(135deg, #1e293b, #2563eb);
}
.privacy-modal-icon {
  font-size: 36px !important;
  color: white !important;
}
.privacy-modal-title {
  margin: 0;
  font-size: 26px;
  font-weight: 700;
  letter-spacing: 0.5px;
  color: white;
}
.privacy-modal-subtitle {
  font-size: 16px;
  opacity: 0.92;
}
.privacy-modal-content {
  padding: 32px 36px 24px 36px;
  color: var(--text-main);
  background: var(--bg-card);
  font-size: 17px;
  line-height: 1.7;
  overflow-y: auto;
  flex: 1;
  min-height: 0;
}
.privacy-modal-section {
  margin-bottom: 24px;
}
.privacy-modal-section-title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  font-weight: 600;
  margin: 0;
  color: #2563eb;
}
.privacy-modal-list {
  margin: 12px 0 0 22px;
  padding: 0;
}
.privacy-modal-contact {
  margin-left: 2px;
  font-size: 16px;
}
.privacy-modal-email {
  color: #2563eb;
  text-decoration: underline;
}
.privacy-modal-disclaimer {
  font-size: 14px;
  color: #666;
  margin-bottom: 18px;
  margin-top: 10px;
}
body[data-theme="dark"] .privacy-modal-disclaimer {
  color: #aaa;
}
.privacy-modal-footer {
  background: var(--bg-card);
  border-top: 1px solid #eee;
  padding: 18px 36px;
  display: flex;
  justify-content: center;
  position: sticky;
  bottom: 0;
  z-index: 2;
}
body[data-theme="dark"] .privacy-modal-footer {
  border-top: 1px solid #222b;
}
.privacy-modal-close-btn {
  width: 100%;
  max-width: 400px;
  font-weight: 700;
  font-size: 20px;
  padding: 18px 0;
  border-radius: 10px;
  letter-spacing: 1px;
  box-shadow: 0 2px 8px rgba(37,99,235,0.10);
  transition: background 0.2s;
  background: linear-gradient(90deg, #2563eb 60%, #1e40af 100%);
  color: #fff;
  border: none;
}
body[data-theme="dark"] .privacy-modal-close-btn {
  box-shadow: 0 2px 8px rgba(37,99,235,0.25);
}

/* Responsive adjustments */
@media (max-width: 900px) {
  .privacy-modal-container {
    max-width: 98vw;
  }
  .privacy-modal-header,
  .privacy-modal-content,
  .privacy-modal-footer {
    padding-left: 16px;
    padding-right: 16px;
  }
  .privacy-modal-header {
    padding-top: 24px;
  }
  .privacy-modal-footer {
    padding-top: 16px;
    padding-bottom: 16px;
  }
}
@media (max-width: 600px) {
  .privacy-modal-container {
    max-width: 100vw;
    border-radius: 8px;
  }
  .privacy-modal-header,
  .privacy-modal-content,
  .privacy-modal-footer {
    padding-left: 8px;
    padding-right: 8px;
  }
  .privacy-modal-header {
    padding-top: 16px;
  }
  .privacy-modal-footer {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .privacy-modal-title {
    font-size: 20px;
  }
}

/* Footer Instructions Section Styles */
.instructions-section {
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.05) 0%, rgba(30, 64, 175, 0.05) 100%);
  border-radius: 12px;
  padding: 24px;
  border: 1px solid rgba(37, 99, 235, 0.1);
  position: relative;
  overflow: hidden;
}
.instructions-section-icon-bg {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.1) 0%, rgba(30, 64, 175, 0.1) 100%);
  border-radius: 0 12px 0 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.instructions-section-icon {
  color: #2563eb !important;
  font-size: 20px !important;
}
.instructions-section-title {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 16px 0;
  color: #2563eb;
}
.instructions-section-info-icon {
  font-size: 16px !important;
}

@media (max-width: 600px) {
  .instructions-section {
    padding: 12px;
    border-radius: 8px;
  }
  .instructions-section-title {
    font-size: 16px;
  }
  .instructions-section-icon-bg {
    width: 40px;
    height: 40px;
    border-radius: 0 8px 0 40px;
  }
  .instructions-section-icon {
    font-size: 16px !important;
  }
} 

@media (max-width: 800px) {
  .footer-sections {
    display: block !important;
    width: 100%;
  }
  .footer-section {
    width: 100% !important;
    box-sizing: border-box;
    margin-bottom: var(--space-4);
  }
} 