/* ConfirmModal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-overlay.active {
  display: flex;
}

.modal-container {
  background-color: var(--bg-card);
  border-radius: var(--radius);
  box-shadow: var(--shadow-lg);
  width: 90%;
  max-width: 700px;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.modal-content {
  padding: var(--space-5);
  overflow-y: auto;
  flex: 1 1 auto;
}

.modal-header {
  padding: var(--space-5) var(--space-5) 0 var(--space-5);
  border-bottom: 1px solid var(--border-color);
}

.modal-title {
  margin-top: 0;
  margin-bottom: var(--space-4);
  color: var(--text-main);
  font-size: 1.25rem;
}

.modal-body {
  padding: var(--space-5);
}

.modal-message {
  margin-bottom: var(--space-5);
  color: var(--text-secondary);
  font-size: 1rem;
  line-height: 1.5;
}

.modal-footer {
  padding: var(--space-5);
  border-top: 1px solid var(--border-color);
  display: flex;
  justify-content: flex-end;
  gap: var(--space-3);
}

.modal-actions {
  display: flex;
  gap: var(--space-3);
  justify-content: flex-end;
  margin-top: var(--space-4);
}

.modal-actions .btn {
  min-width: 120px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-2) var(--space-4);
  margin: var(--space-1) 0;
}

/* ConfirmModal Responsive Styles */
.confirm-modal-container {
  max-width: 600px;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-card);
  margin: 0 auto;
  width: 95vw;
}

.confirm-modal-header {
  padding: 32px 32px 0 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.confirm-modal-title {
  margin: 0;
  font-size: var(--text-xl);
  font-weight: 600;
  color: var(--text-main);
}

.confirm-modal-body {
  overflow-y: auto;
  flex: 1;
  padding: 32px;
}

.confirm-modal-footer {
  padding: 24px 32px;
  border-top: 1px solid #eee;
  background: var(--bg-card);
  position: sticky;
  bottom: 0;
  z-index: 2;
}

.confirm-modal-actions {
  margin: 0;
  display: flex;
  gap: 16px;
  justify-content: flex-end;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .confirm-modal-container {
    max-width: 98vw;
    padding: 0;
  }
  .confirm-modal-header,
  .confirm-modal-body,
  .confirm-modal-footer {
    padding-left: 16px;
    padding-right: 16px;
  }
  .confirm-modal-header {
    padding-top: 24px;
  }
  .confirm-modal-footer {
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

@media (max-width: 480px) {
  .confirm-modal-container {
    max-width: 100vw;
    border-radius: var(--radius-md);
  }
  .confirm-modal-header,
  .confirm-modal-body,
  .confirm-modal-footer {
    padding-left: 8px;
    padding-right: 8px;
  }
  .confirm-modal-header {
    padding-top: 16px;
  }
  .confirm-modal-footer {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .confirm-modal-title {
    font-size: var(--text-lg);
  }
}

@media (max-width: 389px) {
  .modal-actions {
    flex-direction: column;
    gap: var(--space-3);
    width: 100%;
  }
  
  .modal-actions .btn {
    width: 100%;
    min-width: auto;
    padding: var(--space-2) var(--space-4);
    margin: var(--space-2) 0;
  }
  
  .modal-footer {
    flex-direction: column;
  }
} 